











import {EmitMixin, StyleMixin} from "../../utils/mixins";
import PlTreeNode from "./pl-tree-node.vue";
import {TreeMark, TreeNode} from "./tree";

/**
 * 拖拽放置的类型
 * @author  韦胜健
 * @date    2020/4/1 17:15
 */
enum DropType {
    prev = 'prev',
    inner = 'inner',
    next = 'next',
    null = 'null',
}

export default {
    name: "pl-tree",
    components: {PlTreeNode},
    mixins: [
        EmitMixin,
        StyleMixin,
    ],
    provide() {
        return {
            plTree: this,
        }
    },
    props: {
        data: {type: Array},                                        // 树形结构数据
        loading: {type: Boolean},                                   // 当前是否处于loading状态
        nodeIcon: {type: Function},                                 // 节点图标

        // 部分key
        keyField: {type: String, required: true},                   // 每一个树节点用来标识的唯一树形
        labelField: {type: String},                                 // 树节点展示文本对应字段
        childrenField: {type: String},                              // 树节点对应子节点数据对应字段

        // 普通属性
        renderContent: {type: Function},                            // 树节点内容渲染函数
        filterNodeMethod: {type: Function},                         // 对树节点进行筛选的方法，返回true表示可以显示，返回false表示隐藏
        highlightCurrent: {type: Boolean, default: true},           // 是否高亮当前选中节点
        currentKey: {type: String},                                 // 当前选中节点的key

        // 展开相关属性
        emptyText: {type: String, default: '暂无数据'},              // 没有子节点的时候展示的文本
        defaultExpandAll: {type: Boolean},                          // 是否默认展开所有节点
        according: {type: Boolean},                                 // 是否每次只展开一个同级的树节点
        expandIcon: {type: String},                                 // 树展开图标
        intent: {type: Number, default: 14},                        // 相邻级节点水平缩进距离，默认16，单位px
        lazy: {type: Boolean},                                      // 是否懒加载子节点数据
        isLeaf: {type: Function},                                   // 判断树节点是否为叶子节点的函数，仅在lazy模式有效
        getChildren: {type: Function},                              // 加载子节点数据的函数，仅当 lazy 为true时有效

        renderAfterExpand: {type: Boolean, default: true},          // 是否在第一次展开节点之后才渲染内容
        expandOnClickNode: {type: Boolean, default: null},          // 是否点击树节点的时候展开子节点
        autoExpandParent: {type: Boolean, default: true},           // 是否展开节点的时候，自动展开父节点

        // 勾选相关属性
        showCheckbox: {type: Boolean},                              // 是否展示勾选框
        checkOnClickNode: {type: Boolean},                          // 是否点击树节点的时候选中节点
        checkStrictly: {type: Boolean},                             // 在显示复选框的情况下，是否严格遵循父子互不关联的做法，默认为false
        isCheckable: {type: Function},                              // 当即将选中树节点时，判断是否可以选中该树节点

        // 拖拽属性
        draggable: {type: Boolean},                                 // 是否可拖拽
        allowDrag: {type: Function},                                // 判断节点是否可以拖拽
        allowDrop: {type: Function},                                // 判断目标节点能够被放置
    },
    emitters: {
        emitNodeClick: Function,
        emitCurrentChange: Function,

        emitExpandChange: Function,
        emitExpand: Function,
        emitCollapse: Function,

        emitCheckChange: Function,
        emitCheck: Function,
        emitUncheck: Function,

        emitDragStart: Function,
        emitDragEnter: Function,
        emitDragLeave: Function,
        emitDragOver: Function,
        emitDragEnd: Function,
        emitDrop: Function,
    },
    watch: {
        /**
         * 单向绑定currentKey
         * @author  韦胜健
         * @date    2020/4/1 9:30
         */
        currentKey(val: string): void {
            this.p_currentKey = val
        },
        /**
         * data发生变化之后，清理 mark中无效的数据
         * @author  韦胜健
         * @date    2020/4/1 9:30
         */
        data: {
            deep: true,
            handler(val) {
                this.p_data = val

                this.$nextTick(() => {
                    // 清理删除的数据
                    Object.values(this.mark).forEach((mark: TreeMark) => {
                        if (mark.formatCount !== this.formatCount) {
                            this.$delete(this.mark, mark.key)
                        }
                    })
                })
            },
        },
    },
    data() {
        const p_data = this.data;                                                           // 内置树形数据data
        const p_currentKey: string = this.currentKey                                        // 当前选中的key
        const p_loading: boolean = false                                                    //  内置，当前是否处于loading状态
        const mark: { [key: string]: TreeMark } = {}                                        // 标记映射
        const formatCount: number = 0;                                                      // 当前格式化数据的时候，数据的版本，用来清理mark中不需要保存的数据
        let rootTreeNode: TreeNode = new TreeNode({}, this, 0);                             // 根节点 treeNode对象

        const dragState: {
            show: boolean,
            indicatorStyles: { top?: number, width?: number, left?: number },
            dropInnerKey: string,
            dropType: DropType,
            dropTreeNode: TreeNode,
            dragTreeNode: TreeNode,
            dragstart: Function,
            dragend: Function,
            dragover: Function,
            reflow: boolean,
        }
            = {
            show: false,                                                                    // 当前拖拽指示器是否展示
            indicatorStyles: {},                                                            // 当前指示器样式
            dropInnerKey: null,                                                             // 当前拖拽，即将放入内部的节点的key
            dropType: DropType.null,                                                        // 当前拖拽在目标节点的位置
            dropTreeNode: null,                                                             // 当前放置目标节点的treeNode对象
            dragTreeNode: null,                                                             // 当前拖拽目标节点的treeNode对象
            reflow: false,                                                                  // 当前是否正在重新渲染，重新渲染的时候不要展开动画
            /*---------------------------------------drag listener-------------------------------------------*/
            dragstart: (e) => {
                e.stopPropagation()
                e.dataTransfer.effectAllowed = 'move'

                const dragTreeNode = this.getTreeNodeFromEl(e.currentTarget)

                if (!this.isAllowDrag(dragTreeNode, e)) {
                    e.preventDefault()
                    return
                }

                dragState.dragTreeNode = dragTreeNode
            },
            dragend: (e) => {
                e.stopPropagation()

                dragState.show = false
                dragState.dropInnerKey = null
                dragState.indicatorStyles = {}

                const {dropTreeNode, dragTreeNode, dropType} = dragState

                if (!!dropTreeNode) {
                    dragTreeNode.removeSelf()
                    const dropRelativeData = dropTreeNode.parent.childrenData

                    dragState.reflow = true
                    switch (dropType) {
                        case DropType.prev:
                            // console.log(`添加到 ${dropTreeNode.label} 之前`)
                            dropTreeNode.splice(dropRelativeData.indexOf(dropTreeNode.data), 0, dragTreeNode.data)
                            break
                        case DropType.inner:
                            // console.log(`添加到 ${dropTreeNode.label} 内部`)
                            dropTreeNode.push(dragTreeNode.data)
                            break
                        case DropType.next:
                            // console.log(`添加到 ${dropTreeNode.label} 之后`)
                            dropTreeNode.splice(dropRelativeData.indexOf(dropTreeNode.data) + 1, 0, dragTreeNode.data)
                            break
                        default:
                            // console.log(`无任何变化`)
                            break
                    }
                    this.refreshCheckStatus()
                    setTimeout(() => dragState.reflow = false, 100)
                }
            },
            /*---------------------------------------drop listener-------------------------------------------*/
            dragover: (e) => {
                e.stopPropagation()
                e.preventDefault()
                e.dataTransfer.dropEffect = 'move'

                const treeNode = this.getTreeNodeFromEl(e.currentTarget)

                let nothing = () => {
                    e.dataTransfer.dropEffect = 'none'
                    dragState.show = false
                    dragState.dropInnerKey = null
                    dragState.indicatorStyles = {}
                    dragState.dropType = DropType.null
                    dragState.dropTreeNode = null
                }

                // 如果当前 over 的节点为拖拽节点的子节点，则什么事也不干，清空标记信息并且什么也不做
                if (treeNode === dragState.dragTreeNode) {
                    nothing()
                    return;
                } else {
                    let containsFlag = false
                    this.iterateAll(dragState.dragTreeNode.children, (child) => {
                        if (child === treeNode) {
                            containsFlag = true
                        }
                    })
                    if (containsFlag) {
                        nothing()
                        return;
                    }
                }

                let content = e.currentTarget.querySelector('.pl-tree-node-content')
                let rect = content.getBoundingClientRect()

                if (!!rect) {
                    dragState.dropTreeNode = treeNode

                    let {height, width, left, top} = rect
                    width -= dragState.dropTreeNode.indicatorLeft
                    left += dragState.dropTreeNode.indicatorLeft

                    let deltaY = e.clientY - top
                    let allowDrop;


                    if (deltaY < height / 4) {
                        // 上
                        allowDrop = this.isAllowDrop(dragState.dragTreeNode, dragState.dropTreeNode, DropType.prev, e)
                        if (!allowDrop) return nothing()

                        dragState.dropType = DropType.prev
                        dragState.show = true
                        dragState.dropInnerKey = null
                        dragState.indicatorStyles = {top, width, left}
                    } else if (deltaY < height * (3 / 4)) {
                        // 中
                        allowDrop = this.isAllowDrop(dragState.dragTreeNode, dragState.dropTreeNode, DropType.inner, e)
                        if (!allowDrop) return nothing()

                        dragState.dropType = DropType.inner
                        dragState.show = false
                        dragState.indicatorStyles = {}
                        dragState.dropInnerKey = treeNode.key

                    } else {
                        // 下
                        allowDrop = this.isAllowDrop(dragState.dragTreeNode, dragState.dropTreeNode, DropType.next, e)
                        if (!allowDrop) return nothing()

                        if (treeNode.isExpand && !!treeNode.children && treeNode.children.length > 0) {
                            // 节点已经展开，并且有子节点，表示插入到第一个子节点之前
                            let firstChildTreeNodeDom = undefined

                            if (this.$options.name === 'pl-tree') {
                                firstChildTreeNodeDom = e.currentTarget.querySelector('.pl-tree-node')
                            } else if (this.$options.name === 'pl-virtual-tree') {
                                firstChildTreeNodeDom = e.currentTarget.nextSibling
                            } else {
                                console.warn(`can't recognise ${this.$options.name}`)
                                return nothing
                            }

                            content = firstChildTreeNodeDom.querySelector('.pl-tree-node-content')
                            rect = content.getBoundingClientRect()
                            if (!!rect) {
                                dragState.dropTreeNode = this.getTreeNodeFromEl(firstChildTreeNodeDom)

                                width = rect.width
                                left = rect.left
                                top = rect.top

                                width -= dragState.dropTreeNode.indicatorLeft
                                left += dragState.dropTreeNode.indicatorLeft

                                dragState.dropType = DropType.prev
                                dragState.show = true
                                dragState.dropInnerKey = null
                                dragState.indicatorStyles = {top, width, left}
                            }

                        } else {
                            // 否则这时候应该将节点插入到目标节点之后
                            dragState.dropType = DropType.next
                            dragState.show = true
                            dragState.dropInnerKey = null
                            dragState.indicatorStyles = {top: top + height, width, left,}
                        }
                    }
                }
            },
        }
        return {
            p_data,
            p_loading,
            p_currentKey,
            mark,
            formatCount,
            rootTreeNode,
            dragState,
            virtualScrollFlag: false,
        }
    },
    created(): void {
        this.initLazy()

        if (this.defaultExpandAll) {
            this.$nextTick(() => this.expandAll())
        }
    },
    computed: {
        /**
         * 根节点class
         * @author  韦胜健
         * @date    2020/3/31 21:10
         */
        classes() {
            return [
                'pl-tree',
                'pl-tree-node-list',
                {
                    'pl-tree-highlight-current': this.highlightCurrent,
                    'pl-tree-reflow': this.dragState.reflow,
                    'pl-tree-virtual-scrolling': this.virtualScrollFlag,
                }
            ]
        },
        /**
         * 格式化 data 树形数据
         * @author  韦胜健
         * @date    2020/3/30 17:15
         */
        formatData(): TreeNode[] {

            if (!this.checkProps()) {
                return []
            }
            if (!this.p_data) {
                return []
            }
            this.formatCount++
            this.rootTreeNode.children = this.p_data.map(item => this.formatNodeData(item, this.formatCount, this.rootTreeNode))
            this.rootTreeNode.data = {[this.childrenField]: this.p_data}
            return this.rootTreeNode.children
        },
        /**
         * 用來派发给开发者的当前展开的keys
         * @author  韦胜健
         * @date    2020/3/30 20:14
         */
        emitExpandKeys(): string[] {
            return (Object.values(this.mark) as TreeMark[]).filter(mark => !!mark.expanded).map(mark => mark.key)
        },
        /**
         * 用来派发给开发者的当前选中的keys
         * @author  韦胜健
         * @date    2020/3/31 15:19
         */
        emitCheckKeys(): string[] {
            return (Object.values(this.mark) as TreeMark[]).filter(mark => !!mark.checked).map(mark => mark.key)
        },
        /**
         * 当前是否处于loading状态
         * @author  韦胜健
         * @date    2020/3/31 10:23
         */
        isLoading() {
            return this.loading || this.p_loading
        },
        /**
         * 拖拽指示器的样式
         * @author  韦胜健
         * @date    2020/4/1 17:39
         */
        indicatorStyles() {
            let styles = {} as any
            const indicatorStyles = this.dragState.indicatorStyles
            if (!!indicatorStyles.left) {
                styles.left = `${indicatorStyles.left + 6}px`
            }
            if (!!indicatorStyles.width) {
                styles.width = `${indicatorStyles.width}px`
            }
            if (!!indicatorStyles.top) {
                styles.top = `${indicatorStyles.top}px`
            }
            return styles
        },
    },
    methods: {
        /*---------------------------------------methods-------------------------------------------*/

        /*select*/
        /**
         * 选中某一个树节点
         * @author  韦胜健
         * @date    2020/3/31 9:26
         */
        setCurrent(key: string) {
            this.p_currentKey = key
            this.emitCurrentChange(this.findTreeNodeByKey(this.p_currentKey))
        },
        /**
         * 获取当前选中节点
         * @author  韦胜健
         * @date    2020/3/31 9:39
         */
        getCurrent(): TreeNode | null {
            if (!this.p_currentKey) return null
            return this.findTreeNodeByKey(this.p_currentKey)
        },

        /*expand*/

        /**
         * 展开树节点
         * @author  韦胜健
         * @date    2020/3/30 18:58
         */
        async expand(keys: string | string[]) {
            await this.handleKeys(keys, async (key: string) => {
                const treeNode = this.findTreeNodeByKey(key)
                if (!treeNode) return
                if (!treeNode.isExpand) {

                    if (
                        this.lazy &&                            // 懒加载模式
                        !this.mark[key].loaded &&               // 未曾加载过子节点数据
                        !treeNode.isLeaf                        // 节点不是叶子节点
                    ) {
                        const children = await this.getChildrenAsync(treeNode)
                        treeNode.setChildren(children || [])
                        await this.$plain.nextTick()
                    }

                    if (this.according) {
                        // 手风琴模式，展开某一个节点的时候，关闭兄弟节点
                        if (!!treeNode.parent && !!treeNode.parent.children) {
                            treeNode.parent.children.forEach((child: TreeNode) => child.key !== treeNode.key && this.collapse(child.key))
                        }
                    }

                    this.setMark(treeNode.key, TreeMark.expanded, true)
                    await this.$plain.nextTick()
                    // console.log('expand ', treeNode.key)
                    this.emitExpand(treeNode)
                    this.emitExpandChange(this.emitExpandKeys)
                }
                if (!!this.autoExpandParent && !!treeNode.parent && treeNode.parent.key) {
                    await this.expand(treeNode.parent.key)
                }
            })
        },
        /**
         * 折叠树节点
         * @author  韦胜健
         * @date    2020/3/30 18:58
         */
        async collapse(keys: string | string[]) {
            await this.handleKeys(keys, async (key: string) => {
                const treeNode = this.findTreeNodeByKey(key)
                if (!treeNode) return
                if (treeNode.isExpand) {
                    this.setMark(treeNode.key, TreeMark.expanded, false)
                    await this.$plain.nextTick()
                    this.emitCollapse(treeNode)
                    this.emitExpandChange(this.emitExpandKeys)
                }
            })
        },
        /**
         * 根据树节点当前的展开状态，反向展开或者收起内容
         * @author  韦胜健
         * @date    2020/3/30 19:19
         */
        toggleExpand(key: string) {
            const treeNode = this.findTreeNodeByKey(key)
            if (!treeNode) return
            if (treeNode.isExpand) {
                this.collapse(key)
            } else {
                this.expand(key)
            }
        },
        expandAll() {
            this.iterateAll(this.formatData, treeNode => this.expand(treeNode.key))
        },
        collapseAll() {
            Object.values(this.mark).forEach((mark: TreeMark) => mark.expanded = false)
        },

        /*check*/

        /**
         * 根据key选中树节点
         * @author  韦胜健
         * @date    2020/3/31 17:33
         */
        async check(keys: string | string[]) {
            await this.handleKeys(keys, async (key: string) => {
                const treeNode = this.findTreeNodeByKey(key)
                if (!treeNode) return
                if (!treeNode.isCheck) {
                    treeNode.check(true)

                    // 父子关联模式下，改变子节点以及父节点状态
                    if (!this.checkStrictly) {
                        // 选中所有子节点
                        this.iterateAll(treeNode.children, (child) => child.check(true))
                        // 更新父节点状态，如果父节点所有的子节点都处于选中状态，则更新父节点为选中状态
                        let parent = treeNode.parent
                        while (!!parent && !!parent.key) {
                            if (parent.children.every(child => child.isCheck)) {
                                parent.check(true)
                                parent = parent.parent
                            } else {
                                break
                            }
                        }
                    }

                    await this.$plain.nextTick()
                    this.emitCheck(treeNode)
                    this.emitCheckChange(this.emitCheckKeys)
                }
            })
        },
        /**
         * 根据key取消选中树节点
         * @author  韦胜健
         * @date    2020/3/31 17:33
         */
        async uncheck(keys: string | string[]) {
            await this.handleKeys(keys, async (key: string) => {
                const treeNode = this.findTreeNodeByKey(key)
                if (!treeNode) return
                if (treeNode.isCheck) {
                    treeNode.check(false)

                    // 父子关联模式下，改变子节点以及父节点状态
                    if (!this.checkStrictly) {
                        // 取消选中所有子节点
                        this.iterateAll(treeNode.children, (child) => child.check(false))
                        // 更新父节点状态，如果父节点所有的子节点都处于非选中状态，则更新父节点为非选中状态
                        let parent = treeNode.parent
                        while (!!parent && !!parent.key) {
                            if (parent.isCheck) {
                                parent.check(false)
                                parent = parent.parent
                            } else {
                                break
                            }
                        }
                    }

                    await this.$plain.nextTick()
                    this.emitCheck(treeNode)
                    this.emitCheckChange(this.emitCheckKeys)
                }
            })
        },
        /**
         * 根据key选中或者取消选中树节点
         * @author  韦胜健
         * @date    2020/3/31 17:33
         */
        toggleCheck(key) {
            const treeNode = this.findTreeNodeByKey(key)
            if (!treeNode) return
            if (!treeNode.isCheckable) return
            if (treeNode.isCheck) {
                this.uncheck(key)
            } else {
                this.check(key)
            }
        },
        /**
         * 选中所有节点
         * @author  韦胜健
         * @date    2020/3/31 17:33
         */
        checkAll() {
            this.iterateAll(this.formatData, (treeNode: TreeNode) => treeNode.check(true))
        },
        /**
         * 取消选中所有节点
         * @author  韦胜健
         * @date    2020/3/31 17:33
         */
        uncheckAll() {
            this.iterateAll(this.formatData, (treeNode: TreeNode) => treeNode.check(false))
        },
        /**
         * 获取选中的数据
         * @author  韦胜健
         * @date    2020/3/31 17:34
         */
        getCheckedData() {
            let ret = []
            this.iterateAll(this.formatData, (treeNode: TreeNode) => {
                if (treeNode.isCheck) {
                    ret.push(treeNode.data)
                }
            })
            return ret
        },

        /*---------------------------------------utils-------------------------------------------*/
        /**
         * 设置标记属性
         * @author  韦胜健
         * @date    2020/3/31 12:20
         */
        setMark(key, attr, value) {
            let mark = this.mark[key]
            if (!mark) {
                mark = new TreeMark(key)
                this.$set(this.mark, key, mark)
            }
            mark[attr] = value
        },
        /**
         * 获取标记属性
         * @author  韦胜健
         * @date    2020/3/31 14:01
         */
        getMark(key, attr) {
            let mark = this.mark[key]
            if (!mark) {
                mark = new TreeMark(key)
                this.$set(this.mark, key, mark)
            }
            return mark[attr]
        },
        /**
         * 遍历所有的treeNode
         * @author  韦胜健
         * @date    2020/3/30 19:30
         */
        iterateAll(treeNodes: TreeNode[], fn, iterateChildren?: Function) {
            if (!treeNodes) return
            treeNodes.forEach(treeNode => {
                fn(treeNode)
                if (!!treeNode.children && (!iterateChildren || iterateChildren(treeNode))) {
                    this.iterateAll(treeNode.children, fn, iterateChildren)
                }
            })
        },
        /**
         * 检查props是否合法
         * @author  韦胜健
         * @date    2020/3/30 18:48
         */
        checkProps() {
            if (!this.keyField) {
                console.error('pl-tree 的 keyField属性不能为空，每一条记录必须要有一个key标识')
                return false
            }
            if (!this.childrenField) {
                console.error('pl-tree 的 childrenKey不能为空')
                return false
            }
            return true
        },
        /**
         * 格式化树节点数据
         * @author  韦胜健
         * @date    2020/3/30 17:16
         */
        formatNodeData(data, formatCount: number, parent?: TreeNode, level: number = 1): TreeNode {
            const treeNode = new TreeNode(data, this, level, parent)
            this.setMark(treeNode.key, TreeMark.treeNode, treeNode)
            this.setMark(treeNode.key, TreeMark.formatCount, formatCount)
            treeNode.children = (treeNode.childrenData || []).map(child => this.formatNodeData(child, formatCount, treeNode, level + 1))
            return treeNode
        },
        /**
         * 通过 key 寻找treeNode
         * @author  韦胜健
         * @date    2020/3/30 20:52
         */
        findTreeNodeByKey(key: string): TreeNode {
            const treeNode = this.getMark(key, TreeMark.treeNode)
            if (!treeNode) {
                console.warn(`无法找到treeNode：${key}`, this.mark)
            }
            return treeNode
        },
        /**
         * 获取子节点数据异步方法
         * @author  韦胜健
         * @date    2020/3/31 15:21
         */
        getChildrenAsync(treeNode: TreeNode | null) {
            return new Promise((resolve) => {
                if (!treeNode.key) {
                    this.p_loading = true
                } else {
                    this.setMark(treeNode.key, TreeMark.loading, true)
                }
                this.getChildren(treeNode, (...results) => {
                    if (!treeNode.key) {
                        this.p_loading = false
                    } else {
                        this.setMark(treeNode.key, TreeMark.loading, false)
                        this.setMark(treeNode.key, TreeMark.loaded, true)
                    }
                    resolve(...results)
                })
            })
        },
        /**
         * 处理keys
         * @author  韦胜健
         * @date    2020/3/31 15:23
         */
        async handleKeys(keys: string | string[], handler: (value: unknown, index: number, array: []) => unknown) {
            keys = Array.isArray(keys) ? keys : [keys]
            return await Promise.all(keys.map(handler))
        },
        getTreeNodeFromEl(el: any) {
            const instance = el.__vue__
            if (!!instance.treeNode) return instance.treeNode
            if (!!instance.$parent && !!instance.$parent.treeNode) return instance.$parent.treeNode
            return null
        },
        /**
         * 刷新节点选中状态
         * @author  韦胜健
         * @date    2020/4/1 22:17
         */
        refreshCheckStatus() {
            if (!this.showCheckbox) return
            if (this.checkStrictly) return;

            const next = (treeNode: TreeNode) => {
                let hasCheck = false
                let hasUncheck = false

                if (!!treeNode.children && treeNode.children) {
                    treeNode.children.forEach(child => {
                        next(child)
                        if (child.isCheck) {
                            hasCheck = true
                        } else {
                            hasUncheck = true
                        }
                    })
                }
                if (hasCheck && !hasUncheck) {
                    // 所有子节点选中
                    if (!treeNode.isCheck) {
                        treeNode.check(true)
                    }
                } else if (hasUncheck) {
                    // 有子节点未选中
                    if (treeNode.isCheck) {
                        treeNode.check(false)
                    }
                }
            }

            this.formatData.forEach(next)
        },
        /**
         * 判断是否可以拖拽
         * @author  韦胜健
         * @date    2020/4/1 23:12
         */
        isAllowDrag(dragTreeNode: TreeNode, event: DragEvent) {
            return !this.allowDrag || this.allowDrag(dragTreeNode, event)
        },
        /**
         * 判断是否可以放置
         * @author  韦胜健
         * @date    2020/4/1 23:12
         */
        isAllowDrop(dragTreeNode: TreeNode, dropTreeNode: TreeNode, dropType: DropType, event) {
            return !this.allowDrop || this.allowDrop(dragTreeNode, dropTreeNode, dropType, event)
        },
        /*---------------------------------------helper-------------------------------------------*/

        async initLazy() {
            if (!this.lazy) {
                return
            }
            this.p_data = await this.getChildrenAsync(this.rootTreeNode)
        },

        /*---------------------------------------handler-------------------------------------------*/
        /**
         * 处理树节点点击展开图标的动作
         * @author  韦胜健
         * @date    2020/3/30 19:02
         */
        onClickExpandIcon(e, treeNode: TreeNode): void {
            e.stopPropagation()
            this.toggleExpand(treeNode.key)
        },
        /**
         * 处理点击节点内容动作
         * @author  韦胜健
         * @date    2020/3/30 19:17
         */
        onClickNodeContent(treeNode: TreeNode): void {
            this.emitNodeClick(treeNode)
            this.setCurrent(treeNode.key)
            if (this.expandOnClickNode !== false) {
                this.toggleExpand(treeNode.key)
            }
            if (this.checkOnClickNode == true) {
                this.toggleCheck(treeNode.key)
            }
        },
        /**
         * 处理点击子节点 checkbox 动作
         * @author  韦胜健
         * @date    2020/3/31 15:06
         */
        onClickCheckbox(e, treeNode) {
            e.stopPropagation()
            this.toggleCheck(treeNode.key)
        },
    },
}
