






export default {
    name: "pl-step-group",
    provide() {
        return {
            plStepGroup: this,
        }
    },
    props: {
        current: {type: [String, Number]},                                // 双向绑定，指定当前步骤条的步骤，在step组件中，可以通过 status 属性覆盖状态
        currentStatus: {type: String},                                  // 当前激活节点的状态
        type: {type: String, default: 'normal'},                        // 步骤条类型，normal、navigation
        vertical: {type: Boolean},                                      // 步骤条是否为纵向
        titleAlignBottom: {type: Boolean},                              // 默认情况下，标题会放在图标右侧，设置该属性可以改为放在图标下面
        mini: {type: Boolean},                                          // 迷你步骤条
        dotIcon: {type: Boolean},                                       // 图标是否采用小圆点替代
    },
    data() {

        // 刷新每个step的index，以便step知道自己处于哪个位置
        const refreshStepIndex = this.$plain.utils.debounce((): void => {
            this.items.forEach(item => item.refreshIndex())
        }, 100)
        return {
            items: [],
            refreshStepIndex,
        }
    },
    computed: {
        classes() {
            return [
                `pl-step-group-${this.vertical ? 'vertical' : 'horizontal'}`,
                {
                    'pl-step-group-title-align-bottom': this.isTitleAlignBottom,
                    'pl-step-group-mini': this.mini,
                    'pl-step-group-dot-icon': this.dotIcon,
                },
            ]
        },
        currentIndex() {
            if (typeof this.current === "number") {
                return this.current
            } else {
                for (let i = 0; i < this.items.length; i++) {
                    const item = this.items[i];
                    if (item.val === this.current) return item.index
                }
            }
        },
        isTitleAlignBottom() {
            return this.titleAlignBottom || this.vertical
        },
    },
    methods: {
        /*---------------------------------------utils-------------------------------------------*/
        addItem(item) {
            this.items.push(item)
            this.refreshStepIndex()
        },
        removeItem(item) {
            this.items.splice(this.items.indexOf(item), 1)
            this.refreshStepIndex()
        },
    },
}
