

































import {EmitMixin, PropsMixinFactory} from "../../utils/mixins";

/**
 * v-if会触发created，会重新刷新index，但是v-show不会，暂时不管v-show
 * @author  韦胜健
 * @date    2020/4/3 10:39
 */

/*节点状态类型*/
enum STATUS {
    wait = 'wait',
    finish = 'finish',
    process = 'process',
    error = 'error',
}

export default {
    name: "pl-step",
    inject: {
        plStepGroup: {},
    },
    mixins: [
        EmitMixin,
        PropsMixinFactory.create({
            title: PropsMixinFactory.Promise,
            subTitle: PropsMixinFactory.Promise,
            content: PropsMixinFactory.Promise,
        })
    ],
    emitters: {
        emitClick: Function,
    },
    props: {
        icon: {type: String},
        status: {type: String},
        title: {type: [String, Object]},
        content: {type: [String, Object]},
        val: {type: String},
    },
    data() {

        /*当前步骤在dom节点中的索引*/
        const index: number = null

        return {
            index,
            STATUS,
        }
    },
    created() {
        this.plStepGroup.addItem(this)
    },
    beforeDestroy() {
        this.plStepGroup.removeItem(this)
    },
    computed: {
        p_icon(): string {
            if (!!this.icon) return this.icon
            return null
        },
        isLast(): boolean {
            return this.index === this.plStepGroup.items.length
        },
        isFirst(): boolean {
            return this.index === 1
        },
        classes() {
            return [
                `pl-step-status-${this.currentStatus}`,
                {
                    'pl-step-has-icon': !!this.p_icon,
                    'pl-step-last': !!this.isLast,
                }
            ]
        },
        currentStatus(): STATUS {
            if (!!this.status) return this.status
            if (this.plStepGroup.currentIndex > this.index) {
                return STATUS.finish
            } else if (this.plStepGroup.currentIndex === this.index) {
                if (!!this.plStepGroup.currentStatus) {
                    return this.plStepGroup.currentStatus
                } else {
                    return STATUS.process
                }
            } else if (this.plStepGroup.currentIndex < this.index) {
                return STATUS.wait
            }
        },
    },
    methods: {
        /*---------------------------------------utils-------------------------------------------*/
        async refreshIndex() {
            await this.$plain.nextTick()
            // @ts-ignore
            this.index = Array.from(this.$el.parentNode.childNodes).filter(item => item.nodeName !== '#comment' && (!item.style || item.style.display !== 'none')).indexOf(this.$el) + 1
        },
    },
}
